// in src/Dashboard.js
import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import SimpleLineChart from "../components/linecharts";
import CustomShapeBarChart from "../components/barcharts";
import CustomShapeBarChartss from "../components/barchartss";
import Randomcharts from "../components/randomcharts";
import PieChart from "../components/piecharts";
import Donutcharts from "../components/donutcharts";
import Pietreecharts from "../components/pietreecharts";

const styles = theme => ({
  root: {
    flexGrow: 1
  },
  paper: {
    padding: theme.spacing.unit * 2,
    textAlign: "center",
    fontWeight: "Bold",
    color: theme.palette.text.secondary
  },
  pont: {
    fontSize: "30px",
    fontWeight: "Bold",
    padding: theme.spacing.unit * 2,
    textAlign: "center"
  },
  line: {
    position: "right"
  }
});

function CenteredGrid(props) {
  const { classes } = props;

  return (
    <div className={classes.root}>
      <Grid container spacing={24}>
        <Grid item xs={12} md={12}>
          <Paper className={classes.pont}>
            Info Grafis Bank Sampah Perjuangan{" "}
          </Paper>
        </Grid>

        <Grid item xs={12} md={12}>
          <Paper className={classes.paper}>
            Total Pembelian Berdasarkan Type-Sampah (Perminggu){" "}
            <CustomShapeBarChart />{" "}
          </Paper>
        </Grid>

        <Grid item xs={12} md={12}>
          <Paper className={classes.paper}>
            Total Pembelian Berdasarkan Jumlah Nasabah (Perminggu){" "}
            <CustomShapeBarChartss />{" "}
          </Paper>
        </Grid>

        <Grid item xs={12} md={12}>
          <Paper className={classes.paper}>
            Info Grafik Pembelian Berdasarkan Type Sampah (Kg){" "}
            <SimpleLineChart />{" "}
          </Paper>
        </Grid>

        <Grid item xs={12} md={6}>
          <Paper className={classes.paper}>
            Persentase Pembelian Type Sampah <Randomcharts />{" "}
          </Paper>
        </Grid>

        <Grid item xs={12} md={6}>
          <Paper className={classes.paper}>
            Persentase Pembelian Type Sampah <Pietreecharts />{" "}
          </Paper>
        </Grid>

        <Grid item xs={12} md={6}>
          <Paper className={classes.paper}>
            Persentase Pembelian Type Sampah <PieChart />{" "}
          </Paper>
        </Grid>

        <Grid item xs={12} md={6}>
          <Paper className={classes.paper}>
            Persentase Pembelian Type Sampah <Donutcharts />{" "}
          </Paper>
        </Grid>
      </Grid>
    </div>
  );
}

CenteredGrid.PropTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(CenteredGrid);
