// in src/users.js
import React, { Fragment } from "react";
import ResetViewsButton from "./ResetViewsButton";
import {
  List,
  Datagrid,
  TextField,
  Filter,
  TextInput,
  ReferenceInput,
  SelectInput,
  // SimpleList,
  Responsive,
  ReferenceField,
  Show,
  // SimpleShowLayout,
  // DateField,
  ShowButton,
  TabbedShowLayout,
  BulkDeleteButton,
  // DeleteButton,
  Tab
  // RichTextField,
  // NumberField,
  // BooleanField,
  // ReferenceManyField,
  // EditButton
} from "react-admin";

const TranFilter = props => (
  <Filter {...props}>
    <TextInput label="Search" source="q" alwaysOn />
    <ReferenceInput
      label="Status"
      source="statusId"
      reference="stransaksi"
      allowEmpty
    >
      <SelectInput optionText="status" />
    </ReferenceInput>
  </Filter>
);

const BulkActionButtons = props => (
  <Fragment>
    <ResetViewsButton label="Reset Views" {...props} />
    {/* Add the default bulk delete action */}
    <BulkDeleteButton {...props} />
  </Fragment>
);

export const ChekTransaksi = props => (
  <List
    title="Cek Transaksi"
    {...props}
    filters={<TranFilter />}
    bulkActionButtons={<BulkActionButtons />}
  >
    <Responsive
      small={
        <Datagrid>
          <TextField label="Nama Penjaul" source="nama" />
          <TextField label="Alamat" source="alamat" />
          <TextField label="Tanggal Pembelian" source="tanggal" />
          <ShowButton />
        </Datagrid>
      }
      medium={
        <Datagrid>
          <TextField label="Nama Penjaul" source="nama" />
          <TextField label="Alamat" source="alamat" />
          <TextField label="Tanggal Pembelian" source="tanggal" />
          <TextField label="Jenis Sampah" source="jenissampah" />
          <TextField label="Jumlah Sampah" source="jumlahsampah" />

          <ReferenceField
            label="Status Transaksi"
            source="statusId"
            reference="stransaksi"
          >
            <TextField source="status" />
          </ReferenceField>
          <ShowButton />
        </Datagrid>
      }
    />
  </List>
);

export const TranShow = props => (
  <Show {...props}>
    {/* <SimpleShowLayout>
      <TextField label="Nama Penjaul" source="nama" />
      <TextField label="Alamat" source="alamat" />
      <TextField label="Tanggal Pembelian" source="tanggal" />
      <TextField label="Kontak" source="hp" />
      <TextField label="Tanggal" source="tanggal" />
      <TextField label="Jenis Sampah" source="jenissampah" />
      <TextField label="Harga Sampah" source="hargasampah" />
      <TextField label="Jumlah Sampah" source="jumlahsampah" />
      <TextField label="Jenis Pembayaran" source="metodepembayaran" />
      <TextField label="Total Pembayaran" source="totalpembayaran" />

      <ReferenceField label="Status" source="statusId" reference="stransaksi">
        <TextField source="status" />
      </ReferenceField> */}

    <TabbedShowLayout>
      <Tab label="Detai Penjual">
        <TextField label="Penjaul" source="nama" />
        <TextField label="Alamat" source="alamat" />
        <TextField label="Tanggal Pembelian" source="tanggal" />
        <TextField label="Kontak" source="hp" />
        <TextField label="Email" source="email" />
      </Tab>
      <Tab label="Detail Barang">
        <TextField label="Tanggal Pembelian" source="tanggal" />
        <TextField label="Jenis Sampah" source="jenissampah" />
        <TextField label="Jumlah Sampah" source="jumlahsampah" />
      </Tab>
      <Tab label="Status Transaksi" path="miscellaneous">
        <ReferenceField
          label="Status Transaksi"
          source="statusId"
          reference="stransaksi"
        >
          <TextField source="status" />
        </ReferenceField>
      </Tab>
      <Tab label="Metode Pembayaran">
        <TextField label="Total Pembayaran" source="totalpembayaran" />
        <TextField source="metodepembayaran" />
      </Tab>
    </TabbedShowLayout>
    {/* </SimpleShowLayout> */}
  </Show>
);
