// in src/users.js
import React, { Fragment } from "react";
import ResetViewsButton from "./ResetViewsButton";
import {
  List,
  Datagrid,
  TextField,
  Filter,
  TextInput,
  ReferenceInput,
  SelectInput,
  // SimpleList,
  Responsive,
  ReferenceField,
  Show,
  SimpleShowLayout,
  SaveButton,
  BulkDeleteButton,
  // DateField,
  ShowButton
  // SimpleForm,
  // Create
  // TabbedShowLayout,
  // DeleteButton,
  // Tab
  // RichTextField,
  // NumberField,
  // BooleanField,
  // ReferenceManyField,
  // EditButton
} from "react-admin";

const TranFilter = props => (
  <Filter {...props}>
    <TextInput label="Search" source="q" alwaysOn />
    <ReferenceInput
      label="Status"
      source="statusId"
      reference="stransaksi"
      allowEmpty
    >
      <SelectInput optionText="status" />
    </ReferenceInput>
  </Filter>
);
const BulkActionButtons = props => (
  <Fragment>
    <ResetViewsButton label="Reset Views" {...props} />
    {/* Add the default bulk delete action */}
    <BulkDeleteButton {...props} />
  </Fragment>
);

export const TrashpurchaseList = props => (
  <List
    title="Pembelian Sampah"
    {...props}
    filters={<TranFilter />}
    bulkActionButtons={<BulkActionButtons />}
  >
    <Responsive
      small={
        <Datagrid>
          <ReferenceField
            label="Nama Penjual"
            source="transactionId"
            reference="transactions"
          >
            <TextField source="nama" />
          </ReferenceField>
          <ReferenceField
            label="Jenis Sampah"
            source="transactionId"
            reference="transactions"
          >
            <TextField source="jenissampah" />
          </ReferenceField>

          <ReferenceField
            label="Jumlah Sampah"
            source="transactionId"
            reference="transactions"
          >
            <TextField source="jumlahsampah" />
          </ReferenceField>
          <ShowButton />
        </Datagrid>
      }
      medium={
        <Datagrid>
          <ReferenceField
            label="Nama Penjual"
            source="transactionId"
            reference="transactions"
          >
            <TextField source="nama" />
          </ReferenceField>
          <ReferenceField
            label="Jenis Sampah"
            source="transactionId"
            reference="transactions"
          >
            <TextField source="jenissampah" />
          </ReferenceField>

          <ReferenceField
            label="Jumlah Sampah"
            source="transactionId"
            reference="transactions"
          >
            <TextField source="jumlahsampah" />
          </ReferenceField>

          <ReferenceField
            label="Tanggal"
            source="transactionId"
            reference="transactions"
          >
            <TextField source="tanggal" />
          </ReferenceField>
          {/* <ReferenceField
            label="Status Transaksi"
            source="statusId"
            reference="stransaksi"
          >
            <TextField source="status" />
          </ReferenceField> */}
          <ShowButton />
        </Datagrid>
      }
    />
  </List>
);

