import React, { Fragment } from "react";
import ResetViewsButton from "./ResetViewsButton";
import {
  List,
  Datagrid,
  TextField,
  TextInput,
  Responsive,
  SimpleForm,
  LongTextInput,
  BulkDeleteButton,
  Create
} from "react-admin";

const BulkActionButtons = props => (
  <Fragment>
    <ResetViewsButton label="Reset Views" {...props} />
    {/* Add the default bulk delete action */}
    <BulkDeleteButton {...props} />
  </Fragment>
);

export const ScheduleList = props => (
  <List
    title="Schedule Pickup"
    {...props}
    bulkActionButtons={<BulkActionButtons />}
  >
    <Responsive
      small={
        <Datagrid>
          <TextField label="Judul" source="judul" />
          <TextField label="Tanggal" source="tanggal" />
          <TextField label="Hari" source="hari" />
          <TextField label="Lokasi Awal" source="lokawal" />
          <TextField label="Lokasi Akhir" source="lokakhir" />
        </Datagrid>
      }
      medium={
        <Datagrid>
          <TextField label="Judul" source="judul" />
          <TextField label="Tanggal" source="tanggal" />
          <TextField label="Hari" source="hari" />
          <TextField label="Lokasi Awal" source="lokawal" />
          <TextField label="Lokasi Akhir" source="lokakhir" />
        </Datagrid>
      }
    />
  </List>
);

export const ScheduleCreate = props => (
  <Create title="Buat Schedule Pickup" {...props}>
    <SimpleForm>
      <TextInput source="judul" />
      <TextInput source="tanggal" />
      <TextInput source="hari" />
      <LongTextInput source="lokawal" />
      <LongTextInput source="lokakhir" />
    </SimpleForm>
  </Create>
);
