import React from "react";
import {
  ComposedChart,
  Area,
  Line,
  Bar,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ResponsiveContainer
} from "recharts";

const data = [
  { name: "Januari", organik: 590, non_organik: 800, kimia: 1400 },
  { name: "Februari", organik: 868, non_organik: 967, kimia: 1506 },
  { name: "Maret", organik: 1397, non_organik: 1098, kimia: 989 },
  { name: "April", organik: 1480, non_organik: 1200, kimia: 1228 },
  { name: "Mei", organik: 1520, non_organik: 1108, kimia: 1100 },
  { name: "Juni", organik: 1400, non_organik: 680, kimia: 1700 }
];

function randomChart() {
  return (
    <ResponsiveContainer
      width="99%"
      height={300}
      margin={{ top: 20, right: 85, bottom: 20, left: 20 }}
    >
      <ComposedChart data={data}>
        <XAxis dataKey="name" fontSize="10px" />
        <YAxis />
        <Tooltip />
        <Legend />
        <CartesianGrid stroke="#f5f5f5" />
        <Area
          type="monotone"
          dataKey="organik"
          fill="#8884d8"
          stroke="#8884d8"
        />
        <Bar dataKey="non_organik" barSize={20} fill="#413ea0" />
        <Line type="monotone" dataKey="kimia" stroke="#ff7300" />
      </ComposedChart>
    </ResponsiveContainer>
  );
}

export default randomChart;
