// in src/App.js
import React from "react";
import { Admin, Resource } from "react-admin";
import { ChekTransaksi, TranShow } from "./Menu/transactions";
import { ScheduleList, ScheduleCreate } from "./Menu/schedules";
import { MyaccountList, AccountEdit, MyAccount } from "./Menu/MyAccount";
import { UpdatehargaList, UpdateCreate, UpdateEdit } from "./Menu/UpdateHarga";
import { TrashpurchaseList, TrashShow } from "./Menu/TrashPurchase";
import { createMuiTheme } from "@material-ui/core/styles";
import index from "@material-ui/core/colors/index";
import Dashboard from "./Menu/Dashboard";
import authProvider from "./authProvider";
//import dataProvider from './dataProvider';
import jsonServerProvider from "ra-data-json-server";
//import simpleRestProvider from "ra-data-json-server";
//import UserIcon from "@material-ui/icons/Group";
import ShoppingCart from "@material-ui/icons/ShoppingCart";
//import List from "@material-ui/icons/List";
import DateRange from "@material-ui/icons/DateRange";
import AccountBox from "@material-ui/icons/AccountBox";
//import PersonAdd from "@material-ui/icons/PersonAdd";
import AddCircleOutline from "@material-ui/icons/AddCircleOutline";
import LibraryBooks from "@material-ui/icons/LibraryBooks";
//import { formatMs } from "@material-ui/core/styles/transitions";

const dataProvider = jsonServerProvider("http://localhost:3000");
// "https://my-json-server.typicode.com/dodinurkasih/jsonserver"
// "http://localhost:3000"
const theme = createMuiTheme({
  palette: {
    primary: index,
    secondary: {
      main: "#00e5ff"
    }
  }
});

const App = () => (
  <Admin
    title
    theme={theme}
    dashboard={Dashboard}
    authProvider={authProvider}
    dataProvider={dataProvider}
  >
    <Resource
      name="transactions"
      show={TranShow}
      list={ChekTransaksi}
      icon={LibraryBooks}
      options={{ label: " Cek Transaksi" }}
    />
    <Resource
      name="pembelian"
      show={TrashShow}
      list={TrashpurchaseList}
      icon={ShoppingCart}
      options={{ label: "Pembelian Sampah" }}
    />
    <Resource name="stransaksi" />
    <Resource
      name="schedule"
      create={ScheduleCreate}
      list={ScheduleList}
      icon={DateRange}
      options={{ label: "Schedule PickUp" }}
    />
    <Resource
      name="myaccounts"
      list={MyaccountList}
      edit={AccountEdit}
      avatar={AccountBox}
      card={MyAccount}
      icon={AccountBox}
      options={{ label: "MyAccount" }}
    />
    <Resource
      name="updateharga"
      list={UpdatehargaList}
      edit={UpdateEdit}
      icon={AddCircleOutline}
      create={UpdateCreate}
      avatar={AccountBox}
      options={{ label: "Update Harga" }}
    />
  </Admin>
);

export default App;
