export const MyAccount = ({ ids, data, basePath }) => (
  <div style={{ margin: "1em" }}>
    {ids.map(id => (
      <Card style={cardStyle}>
        <CardImg top width="100%" src="/tampah.PNG" alt="Card image cap" />
        <CardBody style={body}>
          <h5>
            Nama :
            <TextField record={data[id]} source="nama" />
          </h5>
          <h5>
            Nama Bank:
            <TextField record={data[id]} source="namabank" />
          </h5>
          <h5>
            Alamat:
            <TextField record={data[id]} source="alamat" />
          </h5>
          <h5>
            No.HandPhone:
            <TextField record={data[id]} source="hp" />
          </h5>
          <h5>
            Total Pembelian:
            <TextField record={data[id]} source="totalpembelian" />
          </h5>
        </CardBody>
      </Card>
    ))}
  </div>
);

export const AccountEdit = props => (
  <Edit {...props}>
    <SimpleForm>
      <TextInput source="nama" />
      <TextInput source="namabank" />
      <TextInput source="alamat" />
      <TextInput source="hp" />
      <TextInput source="totalpembelian" />
    </SimpleForm>
  </Edit>
);

export const MyaccountList = props => (
  <List title="My Account" {...props}>
    <MyAccount />
  </List>
);
