import React from "react";
import PropTypes from "prop-types";
import { BarChart, Bar, XAxis, YAxis, CartesianGrid, Cell } from "recharts";
import ResponsiveContainer from "recharts/lib/component/ResponsiveContainer";
const colors = [
  "#ff0000",
  "#82ca9d",
  "#2ca02c",
  "#9467bd",
  "#0000ff",
  "#8c564b",
  "#e377c2",
  "#7f7f7f",
  "#bcbd22",
  "#17becf"
];
const data = [
  { name: "Tembaga", uv: 40, female: 24, male: 24 },
  { name: "Kardus", uv: 30, female: 89, male: 22 },
  { name: "Kaleng", uv: 20, female: 48, male: 22 },
  { name: "Besi Karat", uv: 27, female: 39, male: 20 },
  { name: "Botol Plastik", uv: 18, female: 69, male: 21 },
  { name: "Pecahan Kaca", uv: 23, female: 38, male: 25 },
  { name: "Kabel Bekas", uv: 34, female: 43, male: 21 }
];

const getPath = (x, y, width, height) => {
  return `M${x},${y + height}
          C${x + width / 3},${y + height} ${x + width / 2},${y +
    height / 3} ${x + width / 2}, ${y}
          C${x + width / 2},${y + height / 3} ${x + (2 * width) / 3},${y +
    height} ${x + width}, ${y + height}
          Z`;
};

const TriangleBar = props => {
  const { fill, x, y, width, height } = props;

  return <path d={getPath(x, y, width, height)} stroke="none" fill={fill} />;
};

TriangleBar.propTypes = {
  fill: PropTypes.string,
  x: PropTypes.number,
  y: PropTypes.number,
  width: PropTypes.number,
  height: PropTypes.number
};

function CustomShapeBarChartss() {
  return (
    <ResponsiveContainer width="99%" height={300}>
      <BarChart data={data} margin={{ top: 20, right: 10, left: 1, bottom: 5 }}>
        <XAxis dataKey="name" fontSize="10px" />
        <YAxis />
        <CartesianGrid strokeDasharray="3 3" />
        <Bar
          dataKey="female"
          fill="#8884d8"
          shape={<TriangleBar />}
          label={{ position: "top" }}
        >
          {data.map((entry, index) => (
            <Cell key={`cell-${index}`} fill={colors[index % 20]} />
          ))}
        </Bar>

        <Bar
          dataKey="male"
          fill="#bcbd22"
          shape={<TriangleBar />}
          label={{ position: "top" }}
        >
          {data.map((entry, index) => (
            <Cell key={`cell-${index}`} fill={colors[index % 20]} />
          ))}
        </Bar>
      </BarChart>
    </ResponsiveContainer>
  );
}

export default CustomShapeBarChartss;
